/* eslint-disable no-underscore-dangle */
/* eslint-disable eqeqeq */
import React, { useContext, useRef, useState, useEffect } from 'react';

import EditorJs from 'react-editor-js';
import Axios from 'axios';

import { Button, Modal } from 'react-bootstrap';
import { Redirect } from 'react-router-dom';
import { AuthContext } from '../contexts/auth_context';
import { hexStrColor, invertColor } from '../lib/colors.lib';
import { EDITOR_JS_TOOLS } from '../lib/editorjs.dependencies';
import { getApiLink } from '../lib/env.lib';
import Breakpoint from '../components/breakpoint/breakpoint';

export const randNum = (min, max) => {
  const random = Math.round(Math.random() * (+max - +min) + +min);
  return random;
};

export const getRandomBanner = () => {
  return `https://picsum.photos/id/${randNum(0, 1084)}/1000/400.jpg`;
};
const Editor = (props) => {
  // let key_i = 0;
  const instanceRef = useRef(null);

  const { user_id, user_dp, user_fullname, user_username } = useContext(
    AuthContext,
  );
  const [show, setShow] = useState(false);
  const [data_editor, setData] = useState(null);
  const [mode, setMode] = useState(null);
  const [api, setApi] = useState('');
  const [title, setTitle] = useState('');
  const [preview, setPreview] = useState('');
  const [redir, setRedirect] = useState(<></>);
  const [ready, setReady] = useState(false);
  const [articleId, setArticleId] = useState('');
  const [banner, setBanner] = useState('NA');
  const [article_tags, setTags] = useState([]);
  const [tagsInput, setTagsInput] = useState();
  const [isImageLoading, setImageLoading] = useState(true);
  const [instructionFocused, setInstructionFocused] = useState(0);
  function handleTagsAddition(e) {
    const val = e.target.value;
    const lastChar = val[val.length - 1]; // take only the last character
    if (lastChar === ' ') {
      // check if the last character is a <space>

      if (article_tags.length < 5) {
        if (val.trim().length >= 3 && val.trim().length <= 20) {
          const t = article_tags;
          if (t.indexOf(val.trim().toLowerCase()) === -1) {
            setTags(t.concat(val.trim().toLowerCase()));
          } else {
            props.notyf.open({
              type: 'warn',
              message: 'The tag has already been added.',
            });
          }
        } else {
          props.notyf.error('Invalid tag');
        }
      } else {
        props.notyf.error('Only upto 5 tags are allowed on a single article.');
      }
      setTagsInput('');
    } else {
      setTagsInput(val);
    }
  }

  function handleTagRemoval(val) {
    const i = article_tags.indexOf(val);
    if (i !== -1) {
      const t = article_tags;
      t.splice(i, 1);
      setTags(t);
      handleSave();
    } else {
      props.notyf.error('Tag not found');
    }
  }

  function handleOpen() {
    setShow(true);
  }

  function validatePublish() {
    if (title.trim() == '' || title.trim().length > 120) {
      props.notyf.error('Invalid article title');
      return;
    }

    if (data_editor.blocks.length > 0) {
      if (
        data_editor.blocks[0].type === 'paragraph' ||
        data_editor.blocks[0].type === 'header'
      ) {
        handleOpen();
      } else {
        props.notyf.error('Invalid article content');
      }
    } else {
      props.notyf.error('Invalid article content');
    }
  }
  function handleClose() {
    setShow(false);
  }
  async function handleSave() {
    const savedData = await instanceRef.current.save();
    if (
      savedData.blocks.length > 0 &&
      savedData.blocks != undefined &&
      savedData.blocks != null
    ) {
      // if (
      //   savedData.blocks[0].type === 'paragraph' ||
      //   savedData.blocks[0].type === 'header'
      // )
      if (savedData.blocks[0].data) {
        if (savedData.blocks[0].data.text) {
          setPreview(savedData.blocks[0].data.text);
        }
      }
    }
    setData(savedData);
    // console.log('savedData', savedData);
  }

  function handleBtnClick() {
    handleSave();
    if (title.trim() == '' || title.trim().length > 120) {
      props.notyf.error('Invalid article title');
      return;
    }

    if (data_editor.blocks.length > 0) {
      // if (
      //   data_editor.blocks[0].type === 'paragraph' ||
      //   data_editor.blocks[0].type === 'header'
      // ) {
      const _data_editor = {};
      if (props.mode === 'edit') {
        _data_editor.article_id = articleId;
      }
      // else if (props.mode === 'new') {
      // }

      if (banner !== 'NA') {
        _data_editor.article_banner = banner;
      }
      _data_editor.xAuthToken = props.auth_token;
      _data_editor.title = title;
      _data_editor.content = JSON.stringify(data_editor);
      _data_editor.preview = preview;
      _data_editor.tags = article_tags;

      console.log({ _data_editor });

      Axios.post(`${getApiLink()}/api/v1/${api}`, _data_editor)
        .then((response) => {
          console.log(response);
          if (response.data.success) {
            if (props.mode === 'edit') {
              props.notyf.success('Saved article');
            } else {
              props.notyf.success('Published article');
            }
            setRedirect(
              <>
                <Redirect
                  to={(() =>
                    `/read/${response.data.message.data.article_id}`)()}
                />
              </>,
            );
          }
        })
        .catch((e) => {
          if (e) {
            console.log({ resp: e.response.data });
            if (e.response.data.error.message !== null) {
              props.notyf.error(`${e.response.data.error.message}`);
            } else {
              props.notyf.error('Error occured');
            }
          } else {
            props.notyf.error('Error occured');
          }
        });
      // } else {
      //   props.notyf.error('Invalid article title');
      // }
    } else {
      props.notyf.error('Invalid article content');
    }
  }

  function handleBannerClick() {
    setImageLoading(true);
    if (banner === 'NA') {
      setBanner(getRandomBanner());
    } else {
      setBanner('NA');
    }
  }

  useEffect(() => {
    if (props.mode === 'edit') {
      setMode('Save');
      setApi('updateArticleById');
      if (props.articleData) {
        setTitle(props.articleData.title ? props.articleData.title : '');
        setData(JSON.parse(props.articleData.content));
      } else {
        setData({ time: Date.now(), blocks: [], version: '2.17.0' });
      }
    } else {
      setMode('Publish');
      setApi('createArticle');
      setData({ time: Date.now(), blocks: [], version: '2.17.0' });
      setBanner('NA');
    }
  }, []);

  useEffect(() => {
    if (props.articleData) {
      setReady(true);
      setArticleId(props.articleData.article_id);
      setData(JSON.parse(props.articleData.content));
      setBanner(props.articleData.article_banner);

      if (props.articleData.tags) {
        const t = props.articleData.tags.map((i) => i.tag);
        setTags(t);
      }
    }
  }, [props.articleData]);

  return (
    <>
      {/* <br /> */}
      {redir}
      <div className="container">
        <div className="row">
          <div className="col-md-2">
            {/* <hr className="light"/> */}
            <button
              className="btn btn-primary btn-lg mt-2 mb-2"
              onClick={validatePublish}
              type="button"
            >
              {mode}
            </button>
            <Modal
              show={show}
              onHide={handleClose}
              // size="lg"
              // dialogClassName="modal-90w"
            >
              <Modal.Header closeButton>
                <Modal.Title>{mode} Article</Modal.Title>
              </Modal.Header>
              <Modal.Body>
                Would you like to {mode} this article.
                <br />
              </Modal.Body>
              <Modal.Footer>
                <Button variant="secondary" onClick={handleClose}>
                  Close
                </Button>
                <Button variant="primary" onClick={handleBtnClick}>
                  {mode}
                </Button>
              </Modal.Footer>
            </Modal>
          </div>

          <div className="col-md-8 custom-card2x">
            <div className="w-100 mt-2" />

            {banner !== 'NA' && banner != '' && (
              <>
                <div className="w-100 d-flex flex-row flex-wrap items-center">
                  <div
                    className={`${
                      isImageLoading ? 'preload' : ''
                    } mr-md-2 d-inline-blockx`}
                    style={{
                      height: '120px',
                      maxWidth: '100%',
                      width: '300px',
                      borderRadius: '4px',
                    }}
                  >
                    <img
                      src={banner}
                      alt="&nbsp;"
                      onLoad={() => {
                        setImageLoading(false);
                      }}
                      style={{
                        height: 'auto',
                        width: '100%',
                        borderRadius: '4px',
                      }}
                    />
                  </div>

                  <Breakpoint xs sm>
                    <br />
                  </Breakpoint>
                  <div className="mt-auto mb-auto">
                    <button
                      className="btn btn-sm btn-outline-primary mr-2 d-inline-blockx"
                      onClick={() => {
                        setImageLoading(true);
                        setBanner(getRandomBanner());
                      }}
                      type="button"
                    >
                      Change
                    </button>

                    <button
                      className="btn btn-sm btn-outline-secondary mr-2 d-inline-blockx"
                      onClick={() => {
                        setImageLoading(true);
                        setBanner(getRandomBanner());
                      }}
                      type="button"
                    >
                      Randomize
                    </button>

                    <button
                      className="btn btn-sm btn-outline-danger mr-2 d-inline-blockx"
                      onClick={handleBannerClick}
                      type="button"
                    >
                      Remove
                    </button>
                  </div>
                </div>
              </>
            )}

            {(banner === 'NA' || banner === '') && (
              <>
                <div className="w-100">
                  <div
                    className={`${isImageLoading ? 'preload' : ''} mr-md-2`}
                    style={{
                      height: '120px',
                      maxWidth: '100%',
                      width: '300px',
                      borderRadius: '4px',
                      display: 'grid',
                      placeItems: 'center',
                    }}
                  >
                    <button
                      className="btn btn-outline-primary btn-sm"
                      onClick={handleBannerClick}
                      type="button"
                    >
                      Add Banner
                    </button>
                  </div>
                </div>
              </>
            )}

            <br />

            <textarea
              type="text"
              className="form-control mt-2"
              value={title}
              onChange={(e) => {
                if (e.target.value.length < 120) setTitle(e.target.value);
              }}
              style={{
                fontSize: '28px',
                fontWeight: '600',
                border: 'unset',
                boxShadow: 'unset',
                padding: 'unset',
                background: 'unset',
                // color: '#000',
                // height: '47px',
              }}
              placeholder="Title..."
              rows="1"
            />
            <div className="tags mb-2">
              {article_tags.map((i) => {
                const h = hexStrColor(i);
                const inv = invertColor(h, true);

                console.log({ h, inv });
                return (
                  <>
                    <div
                      className="tag d-inline-block noselect mr-2"
                      style={{
                        backgroundColor: `#${h}`,
                        color: inv,
                        cursor: 'pointer',
                      }}
                      onClick={() => handleTagRemoval(i)}
                    >
                      #{i}
                    </div>
                  </>
                );
              })}

              {article_tags.length > 0 && (
                <>
                  <div className="text-muted" style={{ fontSize: '9px' }}>
                    Click on the tags to remove them
                  </div>
                </>
              )}
            </div>

            {article_tags.length < 5 && (
              <input
                type="text"
                className="form-control"
                style={{
                  // fontSize: '12px',
                  border: 'unset',
                  boxShadow: 'unset',
                  padding: 'unset',
                  background: 'unset',
                }}
                placeholder={(() => {
                  if (article_tags.length == 0) {
                    return 'Add upto 5 tags...';
                  }
                  if (article_tags.length > 0 && article_tags.length < 4) {
                    return `Add ${5 - article_tags.length} more tags...`;
                  }
                  return `Add ${5 - article_tags.length} more tag...`;
                })()}
                value={tagsInput}
                onFocus={() => {
                  setInstructionFocused(1);
                }}
                onBlur={() => {
                  setInstructionFocused(0);
                }}
                onChange={handleTagsAddition}
              />
            )}
            {instructionFocused === 1 && article_tags.length < 5 && (
              <div className="text-muted" style={{ fontSize: '9px' }}>
                Press space to add tags
              </div>
            )}

            <div className="mt-2 mb-2" />

            {((props.mode === 'edit' && ready) || props.mode === 'new') && (
              <>
                <EditorJs
                  placeholder="Write an awesome..."
                  // eslint-disable-next-line no-return-assign
                  instanceRef={(instance) => (instanceRef.current = instance)}
                  onChange={handleSave}
                  tools={EDITOR_JS_TOOLS}
                  data={data_editor}
                />
              </>
            )}
          </div>

          <div className="col-md-2" />
        </div>
      </div>
    </>
  );
};

export default Editor;
